import { useEffect, useRef, useState } from "react";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction";
import { useNavigate } from "react-router-dom";
import { PATH } from "../../../utils/path";
import { Button, Card, Dropdown, Form, Modal } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import moment from "moment";
import { useDispatch, useSelector } from "react-redux";
import { getAllAppointment } from "../../../app/features/appointment/appointment.slice";
import { convertToDateTime } from "../../../components/GenericActions";

export default function FullCalender() {
  const calenderRef = useRef();
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));
  const navigate = useNavigate();
  const [getCalenderRef, setGetCalenderRef] = useState();
  const [selectedOptions, setSelectedOptions] = useState(null);

  const { allAppointment } = useSelector((state) => state.appointment);
  const options = [
    { label: "Missed", value: 205 },
    { label: "Upcoming", value: 204 },
    { label: "Completed", value: 206 },
    { label: "Cancelled", value: 203 },
    { label: "Pending", value: 201 },
  ];

  const events = allAppointment?.data?.map((data) => {
    return {
      doctorId: data?.doctorId,
      doctorName: data?.doctorName,
      patientName: data?.patientName,
      vonageSessionId: data?.vonageSessionId,
      statusId: data?.statusId,
      status: data?.status,
      imageUrl: data?.imageUrl,
      isDoctorMissed: data?.isDoctorMissed,
      isPatientMissed: data?.isPatientMissed,
      scheduleId: data?.scheduleId,
      appointmentDate: data?.appointmentDate,
      patientId: data?.patientId,
      appointmentStartTime: data?.appointmentStartTime,
      appointmentEndTime: data?.appointmentEndTime,
      reasonForAppoinment: data?.reasonForAppoinment,
      appointmentId: data?.appointmentId,
      image: data?.image,
      start: convertToDateTime(data?.appointmentDate, data?.appointmentStartTime),
      // start: data?.appointmentDate?.split("T")[0],
    };
  });

  const eventContent = (info) => {
    return (
      <>
        <div
          className="appointment-slot-background"
          style={{
            backgroundColor:
              info?.event?._def?.extendedProps?.status === "Booked" &&
              moment(info?.event?._def?.extendedProps?.appointmentDate)?.format(
                "YYYY-MM-DD"
              ) >= moment(new Date()).format("YYYY-MM-DD")
                ? "#2269F2"
                : info?.event?._def?.extendedProps?.status === "Missed"
                ? "#F26522"
                : "#44BC19",
          }}
        >
          <b>{`${info?.event?._def?.extendedProps?.appointmentStartTime} - ${info?.event?._def?.extendedProps?.appointmentEndTime}`}</b>
          <p className="m-0">
            {info?.event?._def?.extendedProps?.status === "Booked" &&
            moment(info?.event?._def?.extendedProps?.appointmentDate)?.format(
              "YYYY-MM-DD"
            ) >= moment(new Date()).format("YYYY-MM-DD")
              ? "Upcoming Appointment"
              : info?.event?._def?.extendedProps?.status === "Missed"
              ? "Missed Appointment"
              : "Completed Appointment"}
          </p>
        </div>
      </>
    );
  };
  const [showModal, setShowModal] = useState(false);
  const [showJoinButton, setShowJoinButton] = useState(false);
  const [showRescheduleButton, setShowRescheduleButton] = useState(false);
  const [showDropdown, setShowDropdown] = useState(false);

  const [event, setEvent] = useState(null);
  const handleEventClick = (info) => {
    setEvent(info.event);
    setShowModal(true);
  };
  useEffect(() => {
    if (event) {
      let currentTime = moment(new Date());
      let appointmentTime = new Date(
        `${moment(event?._def?.extendedProps?.appointmentDate).format(
          "YYYY-MM-DD"
        )}T${moment(
          event?._def?.extendedProps?.appointmentStartTime,
          "hh:mm A"
        ).format("HH:mm:ss")}`
      );
      let duration = moment.duration(moment(appointmentTime).diff(currentTime));
      let minutes = duration.asMinutes();
      if (minutes < 10 && minutes > -15) setShowJoinButton(true);
      else setShowJoinButton(false);

      let isShowResculdedButton = false;
      if (minutes <= 60) {
        isShowResculdedButton = false;
      } else {
        if (event?._def?.extendedProps.statusId === 202) {
          isShowResculdedButton = true;
        } else if (
          event?._def?.extendedProps?.statusId === 205 &&
          event?._def?.extendedProps?.isDoctorMissed === true &&
          event?._def?.extendedProps?.isPatientMissed === false
        ) {
          isShowResculdedButton = true;
        }
      }
      setShowRescheduleButton(isShowResculdedButton);
    } else {
      setShowJoinButton(false);
    }
  }, [event]);
  const handleCloseModal = () => {
    setShowModal(false);
  };

  function getDayName(
    date = new Date(event?._def?.extendedProps?.appointmentDate),
    locale = "en-US"
  ) {
    return date?.toLocaleDateString(locale, { weekday: "long" });
  }
  const dayName = getDayName();

  const getCalenderRefFunc = (info) => {
    setGetCalenderRef(info?.current?.calendar?.currentData?.currentDate);
  };

  useEffect(() => {
    const finalData = {
      patientId: user?.userId,
      month: +moment(getCalenderRef).format("M"),
      statusId:
        selectedOptions === "205"
          ? 205
          : selectedOptions === "204"
          ? 204
          : selectedOptions === "206"
          ? 206
          : selectedOptions === "203"
          ? 203
          : selectedOptions === "201"
          ? 201
          : null,
    };
    dispatch(getAllAppointment(finalData));
  }, [dispatch, getCalenderRef, user?.userId, selectedOptions]);

  // function patientTeleConsultation(data) {
  //   // localStorage.setItem("slotDescription", JSON.stringify(data));
  //   localStorage.setItem("slotDescription", JSON.stringify(data));
  //   // navigate(PATH.PATIENT_TELE_CONSULTATION, {
  //   //   state: {
  //   //     slotDescription: data,
  //   //   },
  //   // }
  //   // );
  //   navigate(PATH.PATIENT_TELE_CONSULTATION);
  // }
  // function patientTeleConsultation(data) {
  //   localStorage.setItem("slotDescription", JSON.stringify(data));
  //     navigate(PATH.PATIENT_TELE_CONSULTATION);
  // }

  function patientTeleConsultation(data) {
    localStorage.setItem("slotDescription", JSON.stringify(data));
      navigate(PATH.PATIENT_TELE_CONSULTATION);
  }

  function patientRescheduleAppointment(data) {
    navigate(PATH.DOCTOR_DETAILS, {
      state: {
        appointmentId: data?.appointmentId,
      },
    });
  }

  const handleToggleDropdown = () => {
    setShowDropdown(!showDropdown);
  };

  return (
    <Card className="custom-full-calendar">
      <Card.Body className="p-4" style={{ borderRadius: "12px" }}>
        <span className="fc-header-dropdown">
        <Dropdown show={showDropdown} onToggle={handleToggleDropdown}>
          <Dropdown.Toggle variant="success" id="dropdown-basic">
            Appointments
          </Dropdown.Toggle>
          <Dropdown.Menu className="mt-2">
            {options.map((option) => (
              <Form.Check
                type="checkbox"
                id={option.value}
                label={option.label}
                value={option.value}
                checked={selectedOptions?.includes(option.value)}
                onChange={(e) =>
                  setSelectedOptions(
                    selectedOptions === e.target.value ? null : e.target.value
                  )
                }
              />
            ))}
          </Dropdown.Menu>
        </Dropdown>
        </span>
        <div className="appointment-calender">
          <FullCalendar
            plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
            initialView="dayGridMonth"
            headerToolbar={{
              start: "dayGridMonth,timeGridWeek,timeGridDay",
              center: "prev,title,next",
              end: "",
            }}
            ref={calenderRef}
            datesSet={() => getCalenderRefFunc(calenderRef)}
            weekends={true}
            events={events}
            eventContent={eventContent}
            height={"80vh"}
            now={null}
            allDaySlot={false}
            slotEventOverlap={true}
            eventClick={handleEventClick}
            dayMaxEvents={1}
          />
        </div>
        <Modal
          show={showModal}
          onHide={handleCloseModal}
          centered
          animation={false}
          size="md"
          className="custom-modal"
        >
          <Modal.Header closeButton>
            <Modal.Title className="appointment-modal-title">
              {event?._def?.extendedProps?.status === "Booked" &&
              moment(event?._def?.extendedProps?.appointmentDate)?.format(
                "YYYY-MM-DD"
              ) >= moment(new Date()).format("YYYY-MM-DD")
                ? "Upcoming Appointment"
                : event?._def?.extendedProps?.status === "Missed"
                ? "Missed Appointment"
                : "Completed Appointment"}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p
              style={{
                color: "#1A1A1A",
                fontWeight: 600,
                fontSize: "18px",
              }}
              className="mb-2"
            >
              Appointment
            </p>
            <p
              style={{
                color: "#1A1A1A",
                fontWeight: 400,
                fontSize: "16px",
              }}
            >
              {dayName} -{" "}
              {moment(event?._def?.extendedProps?.appointmentDate)?.format(
                "MMM-D"
              )}
              . {event?._def?.extendedProps?.appointmentStartTime} -{" "}
              {event?._def?.extendedProps?.appointmentEndTime}
            </p>
            <span className="d-flex align-items-center">
              <img src={IMAGES.NEW_PATIENT_GRAY} alt="new patient icon" />
              <p
                className="mb-0 px-2"
                style={{
                  color: "#1A1A1A",
                  fontWeight: 500,
                  fontSize: "16px",
                }}
              >
                {event?._def?.extendedProps?.doctorName}
              </p>
            </span>
            <span>
              <p
                className="mb-0 mt-4"
                style={{
                  color: "#999999",
                  fontWeight: 500,
                  fontSize: "16px",
                }}
              >
                Reason:
              </p>
              <p
                style={{
                  fontWeight: 400,
                  fontSize: "14px",
                  textAlign: "justify",
                }}
              >
                {event?._def?.extendedProps?.reasonForAppoinment
                  ? event?._def?.extendedProps?.reasonForAppoinment
                  : "N/A"}
              </p>
            </span>
            {event?._def?.extendedProps?.statusId === 202 && (
              <Button
                className="w-100 mt-4 bg-dk-purple"
                onClick={() =>
                  patientRescheduleAppointment(event?._def?.extendedProps)
                }
              >
                Reschedule Appointment
              </Button>
            )}
            {event?._def?.extendedProps?.statusId === 202 && (
              <Button
                className="w-100 mt-4 bg-dk-purple"
                onClick={() =>
                  patientTeleConsultation(event?._def?.extendedProps)
                }
              >
                Join Appointment
              </Button>
            )}
          </Modal.Body>
        </Modal>
      </Card.Body>
    </Card>
  );
}
